#root,html,body {
  height: 100%;
}
.App{
  display: flex;
  flex-direction: column;
  min-height: 100%;
  position: relative;
  flex: 1;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.table{
  color: #212529;
}
.thead,.tbody{
  display: inline-block;
  width: 100%;
}
.thead .tr{
  border-top: 1px solid #dee2e6;
}
.tr{
  display: flex;
  border-bottom: 1px solid #dee2e6;
}
.th, .td{
  display: inline-block;
  flex: 1;
  padding: .75rem;
}
.td:first-child, .th:first-child{
    flex: 0;
    min-width: 60px;
}

.th{
  font-weight: bold;
}

.addPage{
  position: absolute;z-index: 1;background: #fff;
  height: 100%;
}

.header{
  height: 60px;
}
.footer{
  height: 60px;
}


.header-item {
  float: left;
  border: 0;
  color: rgba(0,0,0,.7);
  border-right: 1px solid rgba(0,0,0,.125);
  cursor: pointer;
}
.brand{
  font-weight: bold;
  font-size: 18px;
}

.No-Items-Found{
  text-align: center;
}